import {
  doc,
  setDoc,
  onSnapshot,
  deleteDoc,
  serverTimestamp,
} from "firebase/firestore";
import { db } from "./firebase";

// Save/replace full cart for user
export async function saveCartForUser(uid, cartData) {
  const ref = doc(db, "users", uid, "meta", "cart"); // nested doc to avoid top-level collisions
  await setDoc(
    ref,
    { items: cartData.items || [], updatedAt: serverTimestamp() },
    { merge: true }
  );
  return ref;
}

// Read and subscribe to cart for user (returns unsubscribe)
export function subscribeToCart(uid, onChange) {
  const ref = doc(db, "users", uid, "meta", "cart");
  return onSnapshot(ref, (snap) => {
    if (!snap.exists()) return onChange({ items: [] });
    onChange({ ...snap.data() });
  });
}

// Clear cart doc
export async function clearCartForUser(uid) {
  const ref = doc(db, "users", uid, "meta", "cart");
  // remove the cart document entirely
  await deleteDoc(ref);
}
